
@font-face {
  font-family: 'coolvetica';
  src: url("./assets/fonts/coolvetica.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
a {
  text-decoration: none;
}
h1, h2 {
  margin-top: 0;
  text-transform: uppercase;
  font-weight: lighter;
}
p {
  font-size: 1.25em;
}
.section-header {
  display: inline-block;
  border-radius: 25px;
  padding: 5px 30px;
  color: #8c21ff;
  background-color: #2f3243;
}
#app {
  font-family: "coolvetica", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  letter-spacing: 1px;
  color: #585a61;
  font-size: .85em;
}
.link-btn {
  border: 2px solid #8c21ff;
  border-radius: 25px;
  padding: 5px 30px;
  color: #f3f6ff;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
}
.link-btn:hover {
    background-color: #8c21ff;
}



